.profile__avatar{
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 30px;
}
.profile__avatar:hover{
opacity: 0.5;
}

@media screen and (max-width: 500px){
    .profile__avatar{
        align-items: center;
        margin: 0;
    }
}