.popup__input {
    width: 358px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.1;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 0 0 13px 0;
}

@media screen and (max-width: 620px) {
    .popup__input{
        width: 238px;
        padding: 0 0 13px 0;
        margin-top: 30px;
    }
}

